.profile_info {
    display: flex;
    flex-direction: column;
}

.profile_info > span {
    margin-bottom: 10px;
}

.profile_info {
    font-size: 18px;
}

.profile_links {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profile_links > button {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    border-radius: 5px;
    width: 100%;
    padding: 8px 5px;
    font-size: 18px;
}

.profile_links > button > img {
    margin-right: 5px;
}

.github {
    filter: opacity(1) drop-shadow(0 0 0 #ffffff);
}

.profile_links > button:first-child {
    margin-bottom: 10px;
    background-color: #1f2328;
    color: white;
}
